
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap');

* {
	padding: 0;
	margin: 0;
	outline: 0;
	box-sizing: border-box;

	letter-spacing: 2px;
}

html, body, #root {
  height: 100%;
}

body {
	font: var( --light) var( --large-size ) Roboto, sans-serif;
	overflow: hidden;
}

#root {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

svg.curve {
	position: absolute;
	top: 0; 
	left: 0;
	z-index: 0;

	width: 100%;
	height: 100%;
	background: #663366;
	clip-path: url(#curve);
}

select,
input, 
textarea {
	font: var( --normal ) var(--large-size) Roboto, sans-serif;
	line-height: var(--very-large-size);

	border: 1px solid #dcdce6;
	width: 100%;
	padding: var( --small-size ) var( --middle-size );
	cursor: pointer;
	border-radius: var( --small-size );
}

textarea {
	resize: vertical;
	text-transform: uppercase;
}

option {
	display: block;
	cursor: pointer;
}

input[type='button'] {
	border: 0;
	background: #e02041;
	color: white;
	text-transform: uppercase;
	font-weight: bold;
	padding: var( --middle-size ) var( --large-size );
}

h1.title {
	position: relative;
	font-size: 2rem;
	font-weight: var( --normal );

	line-height: 4rem;
	text-align: center;
	color: white;
}

section.container {
	border: var( --small-size ) solid #663366;
	z-index: 1;
	background-color: #fff;
	width: 100%;
	max-width: 800px;
	border-radius: var( --middle-size );
	padding: var( --large-size ) var( --very-large-size ) var( --very-large-size ) var( --very-large-size );
	margin: 0 auto;
}

p.description {
	font-size: 1rem;
	text-align: center;
}

section.container form {
	border-radius: var( --large-size );
	margin-top: var( --very-large-size );
}

section.container p {
  margin-left: var( --large-size );
}

section.container textarea,
section.container input,
section.container p {
  margin-top: var( --large-size );
}

section.row {
	display: flex;
	align-items: center;
}

section.row select {
	flex: 1;
	margin-right: var( --large-size );
	background: white;
}

section.row select ~ select {
	margin-left: var( --large-size );
	margin-right: 0;
}

section.row span {
  font-weight: var( --normal );
}
